{\n            fieldtype: \"Column Break\",\n          },\n          {\n            label: \"Business Name\",\n            fieldname: \"business_name\",\n            fieldtype: \"Data\",\n            default: this.businessName,\n          },\n          {\n            fieldtype: \"Section Break\",\n          },\n          {\n            label: \"Address 1\",\n            fieldname: \"address_line1\",\n            fieldtype: \"Data\",\n            default: this.addressLine1,\n          },\n          {\n            label: \"Address 2\",\n            fieldname: \"address_line2\",\n            fieldtype: \"Data\",\n            default: this.addressLine2,\n          },\n          {\n            label: \"City\",\n            fieldname: \"city\",\n            fieldtype: \"Data\",\n            default: this.city,\n          },\n          {\n            fieldtype: \"Column Break\",\n          },\n          {\n            label: \"State\",\n            fieldname: \"state\",\n            fieldtype: \"Autocomplete\",\n            default: this.state,\n            options: this.country.toLowerCase() === \"india\" ? states : []\n          },\n          {\n            label: \"Country\",\n            fieldname: \"country\",\n            fieldtype: \"Data\",\n            default: this.country,\n            onchange(){\n              // TODO: fix in frappe needed to update dialog options\n              this.value.toLowerCase() === \"india\" ?\n              dialog.set_df_property(\"state\",\"options\", states):\n              dialog.set_df_property(\"state\", \"options\", [])\n            }\n          },\n          {\n            label: \"Postal Code\",\n            fieldname: \"pincode\",\n            fieldtype: \"Data\",\n            default: this.pincode,\n          },\n          {\n            fieldtype: \"Section Break\",\n          },\n        ],\n        primary_action_label: \"Save\",\n        primary_action: async () => {\n          const values = dialog.get_values();\n\n   